import React from 'react';

function Modal({onClose, currentPhoto}) {
    console.log(`Modal currentPhoto : ${currentPhoto.category}`);
    const {name, category, index } = currentPhoto;

    return (
        <div>
            <div className="modalBackdrop">
                <div className="modalContainer">
                    {console.log(`name : ${currentPhoto.name}`)}
                    {console.log(`category :  ${currentPhoto.category}`)}
                    <h3 className="modalTitle">{name}</h3>
                    <img src={require(`../../assets/large/${category}/${index}.jpg`)} alt="current category" />
                    {/* <img src={require(`../assets/large/commercial/0.jpg`)} alt="current category" /> */}
                    <p>
                    Photo Description
                    </p>
                    <button onClick={onClose} type="button">
                    Close this modal
                    </button>
                </div>
            </div>
        </div>
    );
}

export default Modal;



